.pbc-container {
  display: flex !important;
  flex-direction: column;
  position: relative;
  padding-bottom: 0px;
  overflow-x: hidden;
  max-width: 100% !important;
  text-align: center;
  width: 100%;
  height: 100%;
}

.pbc-button-group {
  display: flex !important;
  padding-bottom: 64px !important;
  flex: 1 1 0%;
  flex-direction: column;
  -webkit-box-pack: justify;
  justify-content: space-between;
  padding: 64px 16px 32px;
  height: 100%;
  width: 100%;
}

.pbc-button {
  margin-bottom: 16px !important;
}